font-size: clamp(3rem, 5vw, 5rem);
text-transform: capitalize;
color: #314258;
margin-top: 0;
font-weight: 900;
padding: 1px;
/* text-shadow: 1px 2px 5px black; */
-webkit-user-select: none;
   -moz-user-select: none;
