    height: 40px;
    border: 0;
    background: transparent;
    cursor: pointer;
    margin: 4px auto;
    display: block;
    border-radius: 6px;
    outline: none;
}
#toolbar button:last-child{
    cursor: unset;
}
#toolbar button:hover{
